import React from "react";
import useSWR from "swr";
import ContactMe from "./ContactMe";
import { useSelector } from "react-redux";
import "../CSS/Introduction.css";
import { fetcher, postFetcher } from "../Lib/request";
import DownloadCV from "./DownloadCV";

function Introduction({ Email, Github, Linkedin }) {
    const page = useSelector((state) => state.page.value);

    const message_url = process.env.REACT_APP_BASE_URL + "/api/message/";
    const params = {
        params: {
            usage: "Explore",
        },
    };
    const { data: messagetData } = useSWR([message_url, params], ([url, params]) => fetcher(url, params));

    const resume_url = process.env.REACT_APP_BASE_URL + "/api/resume/";
    const { data: resumeData } = useSWR(resume_url, postFetcher);

    if (page === "Explore" && messagetData && resumeData) {
        return (
            <div className="introductionPanel">
                <h1 className="display-1">{messagetData[0].title}</h1>
                <h3 className="display-3">{messagetData[0].message}</h3>
                <div className="mt-4">
                    <ContactMe Email={Email} Github={Github} Linkedin={Linkedin} />
                    <DownloadCV resumeData={resumeData} />
                </div>
            </div>
        );
    }
}

export default Introduction;
