import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    type: "hide",
    value: "",
};

export const alertSlide = createSlice({
    name: "show alert",
    initialState,
    reducers: {
        Show: (state, value) => {
            state.type = "show";
            state.value = value.payload["value"];
        },
        Hide: (state) => {
            state.type = "hide";
        },
    },
});

// Action creators are generated for each case reducer function
export const { Show, Hide } = alertSlide.actions;

export default alertSlide.reducer;
