import React from "react";
import { useSelector } from "react-redux";
import "../CSS/Alert.css";

function Alert() {
    const alert_type = useSelector((state) => state.alert.type);
    const alert_text = useSelector((state) => state.alert.value);
    if (alert_type === "show") {
        return (
            <div className="alert alert-success alertStyle " role="alert">
                {alert_text}
            </div>
        );
    }
}

export default Alert;
