import React from "react";
import { fetcher } from "./Lib/request";
import NavBar from "./Component/NavBar";
import Background from "./Component/Background";
import Introduction from "./Component/Introduction";
import Alert from "./Component/Alert";
import useSWR from "swr";

function App() {
    const url = process.env.REACT_APP_BASE_URL + "/api/contact-info/";
    const { data: contactData } = useSWR(url, fetcher);

    if (contactData) {
        return (
            <>
                <NavBar Github={contactData[0].github} Linkedin={contactData[0].linkedin} />
                <Background />
                <Introduction
                    Email={contactData[0].email}
                    Github={contactData[0].github}
                    Linkedin={contactData[0].linkedin}
                />
                <Alert />

            </>
        );
    }
}

export default App;
