import axios from "axios";

export async function fetcher(url, params) {
    axios.defaults.headers.common["Authorization"] = "Token " + process.env.REACT_APP_BACKEND_TOKEN;
    if (params) {
        return axios.get(url, params).then((res) => res.data);
    } else {
        return axios.get(url).then((res) => res.data);
    }
}

export async function postFetcher(url, params) {
    axios.defaults.headers.common["Authorization"] = "Token " + process.env.REACT_APP_BACKEND_TOKEN;
    if (params) {
        return axios.post(url, params).then((res) => res.data);
    } else {
        return axios.post(url).then((res) => res.data);
    }
}

export default { fetcher, postFetcher };
