import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    value: "Explore",
};

export const pageSlide = createSlice({
    name: "change page",
    initialState,
    reducers: {
        Explore: (state) => {
            state.value = "Explore";
        },
        Project: (state) => {
            state.value = "Project";
        },
    },
});

// Action creators are generated for each case reducer function
export const { Explore, Project } = pageSlide.actions;

export default pageSlide.reducer;
